<template>
	<view class="user-login">
		<view class="head">
			<view class="bg"></view>
			<view class="logo"></view>
		</view>
		<!-- #ifdef APP-PLUS -->
		<view class="btn" @click="wxLogin">微信授权登录</view>
		<!-- #endif -->

		<!-- #ifdef MP-WEIXIN -->
		<button class="btn" open-type="getUserInfo" @getuserinfo="wxLogin" withCredentials="true">微信授权登录</button>
		<!-- #endif -->
		<view class="txt">若未绑定购了么账号，需要绑定手机号</view>
	</view>
</template>

<script>
import { API } from '@/util/api.js';
export default {
	data() {
		return {
			backpage: '',
			backtype: '',
			params: '',
			url: []
		};
	},
	onLoad(options) {
		console.log(options);
		this.backpage = options.backpage;
		this.backtype = options.backtype;
		if (options) {
			for (let i in options) {
				if (i !== 'backpage' && i !== 'backtype') {
					this.url.push('&' + i + '=' + options[i]);
				}
			}
		}
	},
	methods: {
		wxLogin() {
			uni.login({
				provider: 'weixin',
				success: loginRes => {
					// 获取用户信息
					uni.getUserInfo({
						provider: 'weixin',
						success: infoRes => {
							this.request(API.login,'POST',{
									// #ifdef APP-PLUS
									code: infoRes.userInfo.openId,
									// #endif
									// #ifdef MP-WEIXIN
									code: loginRes.code,
									// #endif
									nickname: infoRes.userInfo.nickName,
									sex: infoRes.userInfo.gender,
									headimgurl: infoRes.userInfo.avatarUrl
								},true).then(res => {
								if (res.code === 10000) {
									uni.setStorageSync('token', res.data.token);
									//判断是否绑定过手机号
									if (res.data.is_bind === 0) {
										uni.navigateTo({
											url: '/pages/user/mobileBind?backpage=' + this.backpage + '&backtype=' + this.backtype + this.url.join('')
										});
									} else {
										if (this.backtype == 1) {
											uni.reLaunch({
												url: this.backpage + '?jump=1' + this.url.join('')
											});
										} else if (this.backtype == 2) {
											uni.switchTab({
												url: this.backpage
											});
										} else {
											uni.switchTab({
												url: '/pages/index/index'
											});
										}
									}
								} else {
									uni.showToast({ title: res.msg, icon: 'none' });
								}
							});
						},
						fail: () => {
							uni.showToast({ title: '授权失败', icon: 'none' });
						}
					});
				},
				fail: () => {
					uni.showToast({ title: '授权失败', icon: 'none' });
				}
			});
		}
	}
};
</script>

<style scoped lang="scss">
@import '../../assets/css/user/login.scss';
</style>
